          <!-- {{shortDetails}} ... -->
          <!-- <router-link class="more-details" :to="sitterUrl" v-if="notSitter">More details</router-link> -->
          <div class="more-details" @click="sitterUrl" v-if="notSitter">More details</div>
        <!-- </p> -->
      <!-- </div> -->
    </div>
